import { useForm } from 'react-hook-form';
import './signup.css';
import { NotificationManager } from 'react-notifications';
import { v4 } from 'uuid';
// npm install uuid

export default({products})=>{

let  {register, formState:{errors}, reset, handleSubmit} = useForm();

const saveUser = (data)=>{
    
  data.id = v4();

    data.img = URL.createObjectURL(data.img[0]);

    products.push(data);

}

return <>
<form id="signup-form" className="text-left" onSubmit={handleSubmit(saveUser)}>
  <div className="signup-form-main-message" />
  <div className="main-signup-form">
    <div className="signup-group">
      <div className="form-group">
        <label htmlFor="lg_username" className="sr-only">
          Product Name
        </label>
        <input
          type="text"
          className="form-control"
          {...register('name')}
          placeholder="product name"
        />
      </div>
      <div className="form-group">
        <label htmlFor="lg_username" className="sr-only">
          Category
        </label>
        <select {...register('city')}     className="form-control">
          <option>Select Category</option>
          <option>Households</option>
          <option>Electronics</option>
          <option>Sports</option>
        </select>
      </div>
      <div className="form-group">
        <label htmlFor="lg_password" className="sr-only">
          Password
        </label>
        <input
          type="file"
          className="form-control"
          {...register('img')}          
        />
      </div>      
    </div>
    <button type="submit" className="login-button">
      Signup
    </button>
  </div>  
    </form>
</>










}