import { createSlice } from "@reduxjs/toolkit";
// systemjs format

let cartSection = createSlice({
    name:'cartSection',
    initialState:{
        products:[  {
          id:1,          
          quantity:0,
          name:"OPPO A4",
            price:3000,
            description:"this product is good, I have used it ",
            img:"https://static-01.daraz.pk/p/1f0bb1dd8d067c4ffc0abecc360ff127.jpg_300x0q75.webp"
          },
          {
            id:2,
            quantity:0,
            name:"Product 2",
            price:4000,
            description:"this product is good, I have used it ",      
            img:"https://pk-live-21.slatic.net/kf/S04f8f2122a4f44e0aa75f5a9fffdeba0b.jpg_300x0q75.webp"
          },
          {
            id:3,
            quantity:0,

            name:"Product 3",
            price:5000,
            description:"this product is good, I have used it ",
            img:"https://static-01.daraz.pk/p/8e7c109fcf2a3dc4c0c3f92ea13079f3.jpg_300x0q75.webp"
          },
          {
            id:4,
            quantity:0,

            name:"Product 4",
            price:6000,
            description:"this product is good, I have used it ",
            img:"https://static-01.daraz.pk/p/619bb81a2900fd13b0d48f61c4771c0c.jpg_300x0q75.webp"
          }
        ],
        orderedProducts:[          
          

        ],        
    },
    reducers:{
      toggleProduct:(puraData, nyaData)=>{

        let meraProduct = puraData.products.find(item=>item.id == nyaData.payload.some)
        
        meraProduct.likedHoyga = !meraProduct.likedHoyga;

        // if(meraProduct.likedHoyga == true){
        //   meraProduct.likedHoyga = false;
        // }else{
        //   meraProduct.likedHoyga = true;
        // }
        
      },
      setProducts:(puraData, nyaData)=>{
        puraData.products = nyaData.payload;
      },
      increaseQuantity:(puraData, nyaData)=>{

        let meraProduct = puraData.orderedProducts.find(item=>item.id == nyaData.payload.id)

        if(!meraProduct.quantity){
          meraProduct.quantity = 1;
        }

        meraProduct.quantity++;
              

      },
      decreaseQuantity:(puraData, nyaData)=>{
        let meraProduct = puraData.orderedProducts.find(item=>item.id == nyaData.payload.id)
        meraProduct.quantity--;
      },
        addToCart:(puraData, nyaData)=>{
          puraData.orderedProducts.push(nyaData.payload);
        },
        removeFromCart:(puraData, nyaData)=>{

          let meraIndex = puraData.orderedProducts.findIndex(item=>item.id == nyaData.payload.id)
          puraData.orderedProducts.splice(meraIndex, 1);


        },        
    }
});

export default cartSection;
export let {setProducts, addToCart, removeFromCart, increaseQuantity, decreaseQuantity, toggleProduct} =  cartSection.actions;