
// npm install axios

import Reset from './components/reset/login';
import axios from 'axios';
import Forget from './components/forget/login';

import { Provider, useDispatch } from 'react-redux';
import {NotificationContainer} from 'react-notifications';
import CreateAd from './components/CreateAd/CreateAd';
import 'react-notifications/lib/notifications.css';
import Signup from './components/signup/signup';
import { Home } from "./components/home/home";
import { Header } from "./components/header/header";
import { BrowserRouter, Routes, Route, Link, useParams } from "react-router-dom";
import '../node_modules/bootstrap/dist/css/bootstrap.min.css'
import '../node_modules/bootstrap/dist/js/bootstrap.bundle'

import ProductDetail from "./components/product-detail/product-detail";
import { useEffect, useState } from "react";
import Users from './components/users/users';

import Login  from './components/login/login';
import Dasboard from './components/dasboard/dasboard';
import { meraStore } from './store/store';
import OrderedItems from './components/cart/ordered-items';
import { setProducts } from './store/cart';
import { loginHogya } from './store/auth';

function Dashboard(){
  return <div>
    <h1>This is dashboard</h1>
    </div>
}




function NotFoundWala(){
  return <div>
    <h2>404 not Found</h2>
  </div>
}



export default()=>{

  let dispatch = useDispatch();

  useEffect(()=>{

    async function some(){

      let resp =  await axios.get('https://fakestoreapi.com/products');
      // console.log(resp.data);

      let sessionCheck  = await axios.post('/token-check',
       {
         token:localStorage.getItem('meraToken')
       }
       ); 

       dispatch(loginHogya(sessionCheck.data))

      //  console.log(sessionCheck.data)
       

      dispatch(setProducts(resp.data));

    }

    some();
    // get
    //post
    //put
    // delet

  }, [])

  return <div>
    
    {/* <Provider store={meraStore}> */}
    <BrowserRouter>    
    <Header />
      <Routes>      

        <Route element={<ProductDetail  />} path="/detail/:abc" />
                   
      {/* // <Route path="/hello/:province/:capital/:uni" element={<FruitWala />} />       */}

      <Route path='/' element={<Home  />} />
        <Route path='/cart' element={<OrderedItems  />} />
        <Route path='/avc' element={<Dashboard />} />
        <Route path="/login" element={<Login  /> } />
        <Route path="/search/:query" element={<Home  /> } />
 
 {/* create krne keliye */}
        <Route path="/signup" element={<Signup  /> } />
        <Route path="/signup/:myName/" element={<Signup  /> } />
 
        <Route path="/users" element={<Users  /> } />
        <Route path="/dashboard" element={<Dasboard  /> } />
        <Route path="/create-ad" element={<CreateAd  /> } />
        <Route path="/forget-password" element={<Forget /> } />
        <Route path="/reset-password/:meraToken" element={<Reset /> } />


        <Route path="*" element={<NotFoundWala /> } />
      </Routes>
    </BrowserRouter>
    <NotificationContainer></NotificationContainer>
    {/* </Provider> */}
  </div>


}