import { useNavigate } from "react-router-dom"
import { useDispatch, useSelector } from "react-redux";
import { userDeleteHogya } from "../../store/auth";
import axios from "axios";
import { useEffect, useState } from "react";

export default()=>{

    let [users, setUsers ] = useState([]);
    let dispatch = useDispatch();
    let move =  useNavigate();

    useEffect(()=>{

        
        axios.get('/users-lao').then(function(abc){
            
            
            setUsers(abc.data);
            console.log(abc.data)
            
        })
        
    }, [])
    // let users = useSelector(store=>{
    //     return store.authSection.users;
    //   })

    return <div className="table-responsive-lg">
        <table className="table">
        {/* <caption>List of users</caption> */}
            <thead>
                <tr>
                <th scope="col">ID</th>
                <th scope="col">Name</th>
                <th scope="col">City</th>
                
                </tr>
            </thead>
            {
                users.map((user, meraIndex)=>{
                    return <tr>
                <td> <img src={user.img || 'no-pic.jpg'} width="100" /></td>
                        <td>{user.id}</td>
                        <td>{user.city}</td>
                        <td>{user.name}</td>
                        <td>
                            <button onClick={()=>{

                                let deleteKardo = window.confirm("Delte krdoon?")

                                if(deleteKardo){

                                    //  ? ko query param kehte h, light sa data send krne keliye use hota h 
                                    // /login?name=ali&city=fsd

                                    // backend per aesy use krenge 
                                    // req.query.name
                                    // req.query.city
                                    axios.delete('/delete-user?abc='+user._id, {}).then(function(resp){
                                        users.splice(meraIndex, 1);
                                        setUsers([...users]) ;
                                    });

                                    // 
                                    // abc([...users]);
                                    // dispatch(userDeleteHogya(meraIndex))
                                }

                            }} className="btn btn-primary">DELETE  </button>

                                <button onClick={()=>{

                                    move('/signup/'+user._id);
                         // <Route path="/signup/:myName" />

                                }} className="btn btn-primary">Edit</button>

                        </td>
                    </tr>
                })
            }
        </table>
    </div>


}