import { useSelector } from 'react-redux';
import './product-detail.css';
import Zoom from 'react-medium-image-zoom'
import 'react-medium-image-zoom/dist/styles.css'
import { useParams } from "react-router-dom";
import { useEffect, useState } from 'react';
import axios from 'axios';

export default function ProductDetail(){

  let [adMilgya, setAdMilgya] = useState({});
  let data = useParams();

    useEffect(()=>{

      axios.get('https://fakestoreapi.com/products/'+data.abc).then((resp)=>{
      console.log(resp.data)
      setAdMilgya(resp.data);
      })

    }, [])

  // let mereProducts = useSelector(store=>store.cartSection.products);
    
    // yeh code baad server/db se connect krega
      // let adMilgya =  mereProducts.find(function(product){
      //   if(product.id == data.abc){
      //     return true;
      //   }
      // })
    
      return <div className="container">
      <div className="card">
        <div className="container-fliud">
          <div className="wrapper row">
            <div className="preview col-md-6">
              <div className="preview-pic tab-content">
                <div className="tab-pane active" id="pic-1">
                  {/* <img src={adMilgya.image} /> */}
                  <Zoom>
    <img
      alt="That Wanaka Tree, New Zealand by Laura Smetsers"
      src={adMilgya.image}
      width="500"
    />
  </Zoom>
                </div>
                <div className="tab-pane" id="pic-2">
                  <img src="http://placekitten.com/400/252" />
                </div>
                <div className="tab-pane" id="pic-3">
                  <img src="http://placekitten.com/400/252" />
                </div>
                <div className="tab-pane" id="pic-4">
                  <img src="http://placekitten.com/400/252" />
                </div>
                <div className="tab-pane" id="pic-5">
                  <img src="http://placekitten.com/400/252" />
                </div>
              </div>
              <ul className="preview-thumbnail nav nav-tabs">
                <li className="active">
                  <a data-target="#pic-1" data-toggle="tab">
                    <img src="http://placekitten.com/200/126" />
                  </a>
                </li>
                <li>
                  <a data-target="#pic-2" data-toggle="tab">
                    <img src="http://placekitten.com/200/126" />
                  </a>
                </li>
                <li>
                  <a data-target="#pic-3" data-toggle="tab">
                    <img src="http://placekitten.com/200/126" />
                  </a>
                </li>
                <li>
                  <a data-target="#pic-4" data-toggle="tab">
                    <img src="http://placekitten.com/200/126" />
                  </a>
                </li>
                <li>
                  <a data-target="#pic-5" data-toggle="tab">
                    <img src="http://placekitten.com/200/126" />
                  </a>
                </li>
              </ul>
            </div>
            <div className="details col-md-6">
              <h3 className="product-title">{adMilgya.name}</h3>
              <div className="rating">
                <div className="stars">
                  <span className="fa fa-star checked" />
                  <span className="fa fa-star checked" />
                  <span className="fa fa-star checked" />
                  <span className="fa fa-star" />
                  <span className="fa fa-star" />
                </div>
                <span className="review-no">41 reviews</span>
              </div>
              <p className="product-description">
                Suspendisse quos? Tempus cras iure temporibus? Eu laudantium cubilia
                sem sem! Repudiandae et! Massa senectus enim minim sociosqu delectus
                posuere.
              </p>
              <h4 className="price">
                current price: <span>{adMilgya.price}</span>
              </h4>
              <p className="vote">
                <strong>91%</strong> of buyers enjoyed this product!{" "}
                <strong>(87 votes)</strong>
              </p>
              <h5 className="sizes">
                sizes:
                <span className="size" data-toggle="tooltip" title="small">
                  s
                </span>
                <span className="size" data-toggle="tooltip" title="medium">
                  m
                </span>
                <span className="size" data-toggle="tooltip" title="large">
                  l
                </span>
                <span className="size" data-toggle="tooltip" title="xtra large">
                  xl
                </span>
              </h5>
              <h5>
                Category: {adMilgya.category}
              </h5>
              <h5 className="colors">
                colors:
                <span
                  className="color orange not-available"
                  data-toggle="tooltip"
                  title="Not In store"
                />
                <span className="color green" />
                <span className="color blue" />
              </h5>
              <div className="action">
                <button className="add-to-cart btn btn-default" type="button">
                  add to cart
                </button>
                <button className="like btn btn-default" type="button">
                  <span className="fa fa-heart" />
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    
    
    }
