import { Link, useNavigate, useParams } from 'react-router-dom';
import './home.css';
import { useSelector, useDispatch } from 'react-redux';
import {addToCart, toggleProduct} from '../../store/cart';
import { NotificationManager } from 'react-notifications';

export function Home(){

  let  move = useNavigate();

  let data= useParams();
  let dispatch = useDispatch();

  let user =  useSelector(store=>store.authSection.currentUser);

  let products = useSelector(store=>{
    return store.cartSection.products;
  })

  if(data.query){
    products = products.filter(product=>product.description.toLowerCase().includes(data.query.toLowerCase()))
  }

    return <div className="productContainer">
      {
        products.map(product=>{
            return <div className="card-item">
            <div onClick={()=>{
              move('/detail/'+product.id);
            }}>
              <div class="card" style={{width: '18rem'}}>
                            <div className='text-center'>
                            <img src={product.image} class="card-img-top" alt="..." />

                            </div>
                            <div class="card-body">
      {true ? <span  onClick={(evt)=>{
                  
                  dispatch(toggleProduct({
                    some:product.id
                  }));

                evt.stopPropagation();
                }} class={"like-btn "+ (product.likedHoyga ? "liked-hua" : '') }></span>

      :  null }

                            {/* <svg aria-label="Favorite icon" class="_08813525"><use href="https://www.olx.com.pk/assets/iconFavoriteUnselected_noinline.e938669ff34db8fd19c07ac3b2b65fcc.svg#icon"></use></svg> */}

                              <h5 class="card-title">{product.name}</h5>
                              <p class="card-text">{product.description.slice(0, 100)}</p>                              
                            </div>
                          </div>
                    </div>
                    {user ? <a onClick={()=>{

                        // alert(2)
                        dispatch(addToCart(product));
                        NotificationManager.success(product.name+ " has been added im the cart")

                        }} class="btn btn-primary add-to-cart-btn">Add to Cart</a> : null}

                          </div>
        })
      }
    </div>
  }