import { useDispatch, useSelector } from "react-redux"
import './ordered-items.css';
import { increaseQuantity, decreaseQuantity, removeFromCart } from "../../store/cart";

export default ()=>{

    let dispatch = useDispatch();

    let orderedWaley =  useSelector(store=>store.cartSection.orderedProducts);

    let totalBill = 0;

    return <div>
        <table className="table">
            <tr>            
                <th></th>    
                <th>Name</th>
                <th>Price</th>
                <th>Total</th>
                <th>Quantity</th>
            </tr>
            {
                orderedWaley.map((item)=>{
                    totalBill += (item.price * (item.quantity || 1));
                    return <tr>
                        <td>
                            <img className="preview-item" src={item.image} />
                        </td>
                        <td>{item.title}</td>
                        <td>{item.price}</td>
                        <td>{item.price * (item.quantity || 1)}</td>
                        <td>
                            {item.quantity || 1}
                            <button onClick={()=>{
                                                                
                               dispatch(increaseQuantity({
                                id:item.id
                               }));

                            }}>+</button>
                            <button  onClick={()=>{
                               dispatch(decreaseQuantity({
                                id:item.id
                               }));
                            }}>-</button>
                            
                        </td>
                        <td>
                            <button onClick={()=>{
                                    dispatch(removeFromCart({
                                        id:item.id
                                    }));
                            }}>REMOVE</button>
                        </td>
                    </tr>
                })
            }

            <tr>
                <td>Total Bill</td>
                <td>{totalBill.toFixed(2)}</td>
            </tr>
        </table>
    </div>

}