import { createSlice } from "@reduxjs/toolkit";


let authSection = createSlice({
    name:'authSection',
    initialState:{
        users:[
            {
                name:'ali',
                password:1234
            }
        ],
        currentUser:null
    },
    reducers:{
        loginHogya:(puraData, nyaData)=>{
            puraData.currentUser = nyaData.payload
        },
        logoutHogya:(puraData, nyaData)=>{
            puraData.currentUser = null;
            localStorage.removeItem('meraToken');
        },
        userDeleteHogya:(puraData, nyaData)=>{
            puraData.users.splice(nyaData.payload, 1)
        },
        signupHogya:(puraData, nyaData)=>{
            puraData.users.push(nyaData.payload)
        },
    }
});

export default authSection;

export let {loginHogya, logoutHogya, signupHogya, userDeleteHogya} = authSection.actions