import { Link, useNavigate } from "react-router-dom";
import './header.css';
import { useSelector, useDispatch } from "react-redux";
import { logoutHogya } from "../../store/auth";
import { useRef } from "react";

export function Header(){

  let move = useNavigate();
  let dispatch = useDispatch();

  let currentUser = useSelector(store=>store.authSection.currentUser);
  let orderedProducts =  useSelector(store=>store.cartSection.orderedProducts.length) 

  let myRef =useRef();

return  <nav className="navbar navbar-expand-lg bg-body-tertiary">
<div className="container-fluid">
  <Link className="navbar-brand" to="/">
e-comerce site
  </Link>
  <button
    className="navbar-toggler"
    type="button"
    data-bs-toggle="collapse"
    data-bs-target="#navbarSupportedContent"
    aria-controls="navbarSupportedContent"
    aria-expanded="false"
    aria-label="Toggle navigation"
  >
    <span className="navbar-toggler-icon" />
  </button>
  <div className="collapse navbar-collapse" id="navbarSupportedContent">
    <ul className="navbar-nav me-auto mb-2 mb-lg-0">
      {currentUser != null ? <li class="nav-item">
       <span> welcome, <b>{currentUser.name}</b></span>
      </li> : null}
    
     {
      currentUser == null ? <>
            <li className="nav-item"> 
                <Link to='/login' className="nav-link active">
                  Login
                </Link>
              </li>

              <li className="nav-item">
              <Link to='/signup' className="nav-link active">
                  Signup
                </Link>
              </li>
        </> : 
        <>
            <li>
                
                <Link to="/cart">
                <span>{orderedProducts}</span>
                  My Products</Link>
            </li>
            <li className="nav-item">
            <Link onClick={()=>{
            
              dispatch(logoutHogya());

            // setCurrentUser(null);
          
          }} to='/login' className="nav-link active">
                Logout
              </Link>
        </li>
              </>
      }

      <li className="nav-item dropdown">
      <Link to='/users' className="nav-link active">
          Users
        </Link>
      </li>
          <li>
          <Link to='/create-ad' className="nav-link active">
          create-ad
        </Link>
            
      </li>
      <li className="nav-item">
        <a className="nav-link disabled" aria-disabled="true">
          Disabled
        </a>
      </li>
    </ul>
      <input

      onKeyDown={(evt)=>{

        if(evt.keyCode == 13){
          move('/search/'+myRef.current.value);
        }

      }}
      ref={myRef}
        className="form-control me-2"
        type="search"
        placeholder="Search"
        aria-label="Search"
      />
      <button onClick={()=>{

        move('/search/'+myRef.current.value);

      }} className="btn btn-outline-success" type="submit">
        Search
      </button>
  </div>
</div>
</nav>


}