
import axios from 'axios';
import { useForm } from 'react-hook-form';
import './login.css';
import { NotificationManager } from 'react-notifications';
import { Link, useNavigate } from 'react-router-dom';
import { loginHogya } from '../../store/auth';
import { useDispatch, useSelector } from 'react-redux';

export default()=>{

// let users = useSelector(store=>store.authSection.users);

let dispatch = useDispatch();
let  move = useNavigate();

let  {register, formState:{errors}, reset, handleSubmit} = useForm();

const saveUser = async (data)=>{


  let resp = await axios.post('/login',data);

  let userMilgya = resp.data;

  // let userMilgya = users.find(user=>{
  //   if(user.name == data.name && user.password == data.password){
  //     return true;
  //   }
  // })

  if(userMilgya){

    localStorage.setItem('meraToken', userMilgya.nishani)

    dispatch(loginHogya(userMilgya.user))
    // setCurrenUser(userMilgya);
    move('/');
    NotificationManager.success("user found")
    // alert('milgya')
  }else{
    NotificationManager.error("user not found")

  }

  

  console.log(data);
  // users.push(data)
}

return <>
<form id="signup-form" className="text-left" onSubmit={handleSubmit(saveUser)}>
  <div className="signup-form-main-message" />
  <div className="main-signup-form">
    <div className="signup-group">
      <div className="form-group">
        <label htmlFor="lg_username" className="sr-only">
          Username
        </label>
        <input
          type="text"
          className="form-control"
          {...register('name')}
          placeholder="username"
        />
      </div>      
      <div className="form-group">
        <label htmlFor="lg_password" className="sr-only">
          Password
        </label>
        <input
          type="password"
          className="form-control"
          {...register('password')}
          placeholder="password"
        />
      </div>      
    </div>
    <button type="submit" className="login-button">
      Login
    </button>
    <Link to="/forget-password">forget password?</Link>
  </div>  
    </form>
</>










}